@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.index_app{
  background-color: rgb(20, 20, 20);
}

*{
    margin: 0px;
    padding: 0px;
    max-width: 100%;
}
.rows{
    position: relative;
    bottom: 20vh;
}
*{
    padding: 0px 0px ;
    margin: 0px 0px ;
    width: 100%;
    /* background-color: rgb(20, 20, 20); */
    /* opacity: -7; */
    /* background: transparent;*/
    /* background-color:white ; */
}
.NetflixOrignals{
    object-fit: contain;
    max-width: 176.7vh;;
    overflow: visible;
    
}
.NetflixOrignals h1{
    font-family: 'Bebas Neue', cursive;
    font-weight: 100;
    color: white;
    padding-left: 60px;
    padding-right: -90px;
}
.netflixMovies_row{
    padding: 10px 60px;
    display: -webkit-flex;
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    overflow-x: scroll;
    overflow-y: hidden;
    transition: -webkit-transform 500ms;
    transition: transform 500ms;
    transition: transform 500ms, -webkit-transform 500ms;
}
.netflixMovies_row::-webkit-scrollbar{
    display: none;
}
.row_img{
    width: 24vh;
    height: 40vh;
    border-radius: 5px;
}
.row_img:hover{
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
}

.other_row_img{
    height: 18vh;
    border-radius: 5px;
    overflow:visible;

}
.other_row_img:hover{
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
    z-index: 1;
}
.banner_div{
    width: 100%;
    object-fit: contain;
    height: 100vh;
}
.banner_button{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}
.banner_content{
    padding-left: 60px;
    padding-top: 38vh;
    width: 50%;
    color: #fff;
    font-family: 'Netflix Sans','Helvetica Neue',Helvetica,Arial,sans-serif;
    font-size: 1.4vw;
}
.banner_content p{
    margin: 20px 0px;
}
.banner_content h1{
    font-family: 'Netflix Sans','Helvetica Neue',Helvetica,Arial,sans-serif;
    font-size: 3.6rem;
}

.banner_button1:hover{
    cursor: pointer;
}


.navbar{
    width: 180vh;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:space-between;
            justify-content:space-between;
    text-align: center;
    padding: 15px 60px;
    position: fixed;
    z-index: 1;
    background: linear-gradient(rgb(37, 37, 37),rgba(37, 37, 37, 0.619), rgba(37, 37, 37, 0.008));
}
.navbar_black{
    width: 180vh;
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content:space-between;
            justify-content:space-between;
    text-align: center;
    padding: 15px 60px;
    position: fixed;
    z-index: 1;
    background: rgb(32, 32, 32);
}
.navbar1{
    display:-webkit-flex;
    display:flex;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}
.nav_list{
    display: grid;
    grid-auto-flow: column;
    grid-row: auto;
    width: 50vh;
    padding-left: 35px;
}
.nav_list li{
    text-decoration: none;
    list-style: none;
    font-family: 'Netflix Sans','Helvetica Neue',Helvetica,Arial,sans-serif;
    color: rgb(221, 219, 219);
    font-size: 0.9rem;
    font-weight: 500;
    transition: -webkit-transform .4s;
    transition: transform .4s;
    transition: transform .4s, -webkit-transform .4s;
}
.nav_list li:hover{
    cursor: pointer;
    color: gray;
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}
.nav_right{
    display:-webkit-flex;
    display:flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    text-align: center;
    width: 60vh;
    color: rgb(221, 219, 219);
    padding-right: 40px;
}
.nav_right_icon:hover{
    cursor: pointer;
    color: gray;
    -webkit-transform: scale(1.01);
            transform: scale(1.01);

}
.nav_right_search{
    background: "rgba(51, 51, 51, 0.5)";
    display: -webkit-flex;
    display: flex;
    text-align: center;
}

